.portfolio {
	position: relative;
	overflow: hidden;
	height: 100vh;
	font-family: $font-anonymous;
	font-size: $font-size-80perc;

	&__header {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 10;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-bottom: 1px solid $bg-medium-dark;
		padding: 0 30px;
		width: 100%;
		height: 35px;
	}

	&__title {
		font-size: $font-size-120perc;
	}

	&__theme-mode {
		position: relative;
		display: flex;
		align-items: center;
		border-radius: 6px;
		width: 46px;
		height: 20px;
		cursor: pointer;

		.portfolio__theme-checkbox {
			width: 0;
			height: 0;
			opacity: 0;
		}

		.portfolio__theme-switcher {
			position: absolute;
			left: 4px;
			top: 3px;
			display: block;
			border-radius: 4px;
			width: 14px;
			height: 14px;
			transition: all 0.4s;
		}
	}

	.portfolio__theme-checkbox:checked ~ .portfolio__theme-switcher {
		left: auto;
		right: 4px;
		background-color: $bg-light;
	}

	&__links {
		padding: 20px 0;

		li + li {
			padding-top: 10px;
		}

		a {
			display: flex;
			align-items: center;
			width: fit-content;
			gap: 5px;
		}

		svg {
			width: 20px;
			height: 20px;
		}
	}

	&__info {
		position: absolute;
		left: 0;
		top: 50%;
		z-index: 10;
		padding-left: 30px;
		max-width: 35%;
		max-height: 35vh;
		opacity: 1;
		transform: translateY(-50%);
		transition: opacity 0.4s;

		h2 {
			margin-bottom: 0;
		}
	}

	&__photo {
		border: 1px solid $bg-medium-dark;
		width: auto;
		height: 160px;
	}

	&__about {
		position: absolute;
		right: 0;
		top: 50%;
		display: flex;
		flex-direction: column;
		padding-right: 30px;
		max-width: 25%;
		opacity: 1;
		transform: translateY(-50%);
		transition: opacity 0.4s;
		gap: 10px;
	}

	&__slider {
		z-index: 5;
		overflow: visible;
		width: 100%;

		.swiper-wrapper {
			// stylelint-disable-next-line
			transition: 0.75s cubic-bezier(0.275, 0.565, 0.375, 0.78) !important;
			will-change: transform;
		}
	}

	&__case {
		position: relative;
		top: 50%;
		display: flex;
		flex-direction: column;
		overflow: hidden;
		border: 1px solid $bg-medium-dark;
		height: 100%;
		max-height: 35vh;
		// stylelint-disable-next-line
		transform: translateY(-50%) !important;
		transition: all ease 0.2s;
		user-select: none;
	}

	.case__name {
		padding: 0 20px;
		transform: translateZ(0);
	}

	.case__img {
		overflow: hidden;
		width: 100%;
		height: 0;
		background-size: contain;
		background-repeat: no-repeat;
		object-fit: contain;

		.animation__img {
			animation: 32s infinite ease-in-out case-scroll;
		}
	}

	.case__technologies {
		display: flex;
		flex-direction: column;
		padding: 20px;
		opacity: 0;
		transform: translateZ(0);
		gap: 10px;

		.techno__sub-title {
			font-weight: 700;
		}
	}

	.case__links {
		display: flex;
		align-items: center;
		margin-top: auto;
		margin-bottom: 0;
		padding: 20px;
		opacity: 0;
		transform: translateZ(0);
		gap: 10px;

		svg {
			width: 5vh;
			height: 5vh;
		}

		li:last-child {
			margin-right: 0;
			margin-left: auto;
		}

		.visit-site-link {
			display: flex;
			align-items: center;
			gap: 5px;

			svg {
				transition: transform 0.4s;
			}
		}
	}
}

.hide {
	z-index: 1;
	opacity: 0;
}

@keyframes case-scroll {
	0% {
		transform: translateY(0);
	}

	50% {
		transform: translateY(-90%);
	}

	100% {
		transform: translateY(0);
	}
}

@media (hover: hover) and (pointer: fine) {
	.visit-site-link:hover {
		svg {
			position: relative;
			transform: translateX(5px);
		}
	}

	.portfolio__case:hover {
		z-index: 2;
		max-height: 75vh;

		.case__img {
			height: 200px;
		}

		.case__technologies,
		.case__links {
			opacity: 1;
		}
	}
}

@media (max-width: 768px) {
	.portfolio {
		display: flex;
		flex-direction: column;
		overflow-y: scroll;
		padding: 30px;
		padding-top: 10vh;
		height: 100vh;
		gap: 20px;

		&__header {
			position: fixed;
			z-index: 20;
		}

		&__info {
			position: static;
			display: grid;
			grid-column: 1 / 3;
			grid-row: 1 / 2;
			grid-template-columns: 1fr 1fr;
			grid-template-rows: auto auto auto;
			padding: 0;
			max-width: 100%;
			max-height: fit-content;
			transform: translateY(0);
			gap: 20px;

			.portfolio__photo {
				grid-column: 1 / 2;
				grid-row: 1 / 4;
				width: 100%;
				height: auto;
			}

			.portfolio__name {
				grid-column: 2 / 3;
				grid-row: 1 / 2;
			}

			.portfolio__profession {
				grid-column: 2 / 3;
				grid-row: 2 / 3;
			}

			.portfolio__links {
				grid-column: 2 / 3;
				grid-row: 3 / 4;
				padding: 0;
			}
		}

		&__about {
			position: static;
			grid-column: 1 / 3;
			grid-row: 2 / 3;
			max-width: 100%;
			transform: translateY(0);
		}

		&__slider {
			grid-column: 1 / 3;
			grid-row: 3 / 4;
			height: 100%;
		}

		&__case {
			top: 0;
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-template-rows: auto auto auto;
			height: 100%;
			transform: translateY(0) !important;

			.case__img {
				grid-column: 1 / 2;
				grid-row: 1 / 3;
				border-right: 1px solid $bg-medium-dark;
				height: 100%;
			}

			.case__name {
				grid-column: 2 / 3;
				grid-row: 1 / 2;
			}

			.case__technologies {
				grid-column: 2 / 3;
				grid-row: 2 / 3;
			}

			.case__links {
				grid-column: 1 / 3;
				grid-row: 3 / 4;
				border-top: 1px solid $bg-medium-dark;
			}

			.case__technologies,
			.case__links {
				opacity: 1;
			}
		}

		.swiper-wrapper {
			flex-direction: column;
			gap: 30px;
		}
	}
}
