.light-mode {
	body {
		background-color: $bg-light;
	}

	.portfolio {
		color: $text-color-dark;
		background-color: $bg-light;

		&__header {
			background-color: $bg-light;
		}

		&__links {
			svg {
				fill: $text-color-dark;
			}
		}

		&__case {
			background-color: $bg-light;
		}
	}

	.portfolio__case:hover {
		box-shadow: inset -5px -5px 9px rgba(255, 255, 255, 0.45), inset 5px 5px 9px rgba(94, 104, 121, 0.3);
	}

	.portfolio__theme-mode {
		box-shadow: inset -5px -5px 9px rgba(255, 255, 255, 0.45), inset 5px 5px 9px rgba(94, 104, 121, 0.3);
		background-color: $bg-light;
	}

	.portfolio__theme-switcher {
		box-shadow: 0 0 0 $text-color-dark;
		background-color: $bg-medium-dark;
	}

	.preloader {
		background-color: $bg-light;

		&__text {
			color: $bg-dark;
		}
	}

	.preloader-box {
		.circle {
			background-color: $bg-dark;
		}
	}
}

.dark-mode {
	body {
		background-color: $bg-dark;
	}

	.portfolio {
		color: $bg-light;
		background-color: $bg-dark;

		&__header {
			background-color: $bg-dark;
		}

		&__links {
			svg {
				fill: $bg-light;
			}
		}

		&__case {
			background-color: $bg-dark;
		}
	}

	.portfolio__theme-mode {
		box-shadow: inset 4px 2px 5px #1f1f1f, inset -12.61px -12.61px 1px #373737;
	}

	.portfolio__theme-switcher {
		background-color: $bg-light;
	}

	.preloader {
		background-color: $bg-dark;

		&__text {
			color: $bg-light;
		}
	}

	.preloader-box {
		.circle {
			background-color: $bg-light;
		}
	}
}
